/* App.css */
.image-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff; /* Or any color you want for the background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.image-popup img {
  max-width: 100%;
  max-height: 100%;
}

.image-popup button {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  font-size: 16px;
  z-index: 1001; /* Above the image */
}

/* App.css */
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
  background-color: #f0f0f0; /* Any background color, or use a background image if you prefer */
}