/* src/Envelope.css */
.envelope {
    font-size: 500px; /* Size of the envelope emoji */
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px; /* Set a fixed height */
    width: 100px; /* Set a fixed width */
    margin: 100px auto; /* Center the envelope */
  }
  